import React, {Component} from 'react'
import Controls from './controls'
import CalcLoan from '../calcs/calc-loan'
import CalcDeposit from '../calcs/calc-deposit'
import CalcSport from '../calcs/calc-sport'

class App extends Component {

    state = {
        currentCalc: 'deposit',
        calcNames: ['deposit', 'sport', 'loan'],
        darkTheme: true
    }

    render() {
        return (<>
            {this.getCalc(this.state.currentCalc)}
            <Controls
                state={this.state}
                switchCalc={this.switchCalc.bind(this)}
                switchTheme={this.switchTheme.bind(this)}
            />
        </>)
    }

    switchCalc(e) {
        this.setState({
            currentCalc: this.state.calcNames[+e.target.value]
        })
    }

    switchTheme(e) {
        const root = document.querySelector('html');
        if (e.target.checked) {
            root.style.setProperty('--main-bg-color', '#333');
            root.style.setProperty('--main-text-color', '#ccc');
            this.setState({
                darkTheme: true
            })
        } else {
            root.style.setProperty('--main-bg-color', '#FAFAFA');
            root.style.setProperty('--main-text-color', '#333');
            this.setState({
                darkTheme: false
            })
        }
    }

    getCalc(calcName) {
        switch (calcName) {
            case 'deposit':
                return <CalcDeposit />
            case 'sport':
                return <CalcSport />
            case 'loan':
                return <CalcLoan />
            default:
                return <CalcDeposit />
        }
    }

    componentDidMount() {
        let fakeEvent = {target: {checked: this.state.darkTheme}};
        this.switchTheme(fakeEvent);
    }
}


export default App