import React, {Component} from 'react'
import './calc.css'
import Header from '../components/header'
import InputNumber from '../components/input-number'
import Checkbox from '../components/checkbox'
import Button from '../components/button'
import OutputString from '../components/output-string'

class CalcDeposit extends Component {

    state = {
        deposit: 10000,
        percent: 6,
        days: 7,
        capitalize: false,
        incomeDay: 0,
        incomeDays: 0,
        incomeMonth: 0,
        incomeYear: 0
    }

    render() {
        return (
            <section className='calc'>
                <form className='input-data' onSubmit={this.calc.bind(this)}>
                    <Header title='Калькулятор доходности депозита' />
                    <InputNumber
                        title='Сумма депозита'
                        value={this.state.deposit}
                        onChangeHandler={this.setDeposit.bind(this)}
                    />
                    <InputNumber
                        title='Ставка депозита / процент на остаток'
                        value={this.state.percent}
                        onChangeHandler={this.setPercent.bind(this)}
                    />
                    <InputNumber
                        title='Дней вклада'
                        value={this.state.days}
                        onChangeHandler={this.setDays.bind(this)}
                    />
                    <Checkbox
                        title='Капитализация (для годового вклада)'
                        checked={this.state.capitalize}
                        onChangeHandler={this.setCapitalize.bind(this)}
                    />
                    <Button
                        title='Рассчитать'
                        className='submit'
                    />
                </form>
                <div className='output-data'>
                    <OutputString
                        title='В день:'
                        value={this.state.incomeDay}
                    />
                    <OutputString
                        title={'За ' + this.state.days + ' дней:'}
                        value={this.state.incomeDays}
                    />
                    <OutputString
                        title='В месяц:'
                        value={this.state.incomeMonth}
                    />
                    <OutputString
                        title='В год:'
                        value={this.state.incomeYear}
                    />
                </div>
            </section>
        )
    }

    setDeposit(e) {
        this.setState({
            deposit: e.target.value
        })
    }

    setPercent(e) {
        this.setState({
            percent: e.target.value
        })
    }

    setDays(e) {
        this.setState({
            days: e.target.value
        })
    }

    setCapitalize(e) {
        this.setState({
            capitalize: e.target.checked
        })
    }

    calc(e) {
        e.preventDefault();
        const {deposit, percent, days, capitalize} = this.state;
        const incomeDay = ((deposit*(percent/365)/100)).toFixed(2);
        const incomeDays = (incomeDay*days).toFixed(2);
        const incomeMonth = ((deposit*(percent/12)/100)).toFixed(2);
        let incomeYear = 0;
        if (capitalize) {
            let month = +incomeMonth;
            let depositWithIncome = +deposit;
            for (var i=1; i<=12; i++) {
                month = +(depositWithIncome*(percent/12)/100).toFixed(2);
                depositWithIncome += month;
            }
            incomeYear = (depositWithIncome-deposit).toFixed(2);
        } else {
            incomeYear = ((deposit*percent)/100).toFixed(2);
        }

        this.setState({
            incomeDay,
            incomeDays,
            incomeMonth,
            incomeYear
        })
    }
}

export default CalcDeposit