import React, {Component} from 'react'
import './calc.css'
import './calc-sport.css'
import db from './calc-sport-db'
import Header from '../components/header'
import Button from '../components/button'
import RadioArea from '../components/radio-area'
import Select from '../components/select'
import OutputString from '../components/output-string'

class CalcSport extends Component {

    state = {
        speedExercise: 42,
        powerExercise: 7,
        enduranceExercise: 46,
        fightExercise: 30,
        speed: 0,
        power: 0,
        endurance: 0,
        fight: 0,
        total: 0,
        grade: 0
    }

    render() {
        return (
            <section className='calc'>
                <Header title='Калькулятор физической подготовки' />
                <form className='input-data' onSubmit={this.calc.bind(this)}>
                    <table className='calc__table table'>
                        <thead>
                            <tr>
                                <td>Упражнение</td>
                                <td>Результат</td>
                                <td>Баллы</td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <RadioArea
                                        name='speedExercise'
                                        checkedValue={this.state.speedExercise}
                                        onChangeHandler={this.setStateOption.bind(this)}
                                        options={[
                                            {value: 40, title: '60м'},
                                            {value: 41, title: '100м'},
                                            {value: 42, title: '10х10м'}
                                        ]}
                                    />
                                </td>
                                <td>
                                    <Select
                                        name='speed'
                                        options={db[this.state.speedExercise]}
                                        onChangeHandler={this.setStateOption.bind(this)}
                                    />
                                </td>
                                <td>{this.state.speed}</td>
                            </tr>
                            <tr>
                                <td>
                                    <RadioArea
                                        name='powerExercise'
                                        checkedValue={this.state.powerExercise}
                                        onChangeHandler={this.setStateOption.bind(this)}
                                        options={[
                                            {value: 4, title: 'Подтягивания'},
                                            {value: 7, title: 'Выходы силой'}
                                        ]}
                                    />
                                </td>
                                <td>
                                    <Select
                                        name='power'
                                        options={db[this.state.powerExercise]}
                                        onChangeHandler={this.setStateOption.bind(this)}
                                    />
                                </td>
                                <td>{this.state.power}</td>
                            </tr>
                            <tr>
                                <td>
                                    <RadioArea
                                        name='enduranceExercise'
                                        checkedValue={this.state.enduranceExercise}
                                        onChangeHandler={this.setStateOption.bind(this)}
                                        options={[
                                            {value: 43, title: '400м'},
                                            {value: 45, title: '1км'},
                                            {value: 46, title: '3км'}
                                        ]}
                                    />
                                </td>
                                <td>
                                    <Select
                                        name='endurance'
                                        options={db[this.state.enduranceExercise]}
                                        onChangeHandler={this.setStateOption.bind(this)}
                                    />
                                </td>
                                <td>{this.state.endurance}</td>
                            </tr>
                            <tr>
                                <td>Рукопашный бой</td>
                                <td>
                                    <Select
                                        name='fight'
                                        options={db[this.state.fightExercise]}
                                        onChangeHandler={this.setStateOption.bind(this)}
                                    />
                                </td>
                                <td>{this.state.fight}</td>
                            </tr>
                        </tbody>
                    </table>
                    <Button className='submit' title='Рассчитать' />
                </form>
                <div className='output-data'>
                    <OutputString
                        title='Оценка'
                        value={this.state.grade}
                    />
                    <OutputString
                        title='Сумма баллов'
                        value={this.state.total}
                    />
                    <OutputString
                        title='Возрастная группа'
                        value='2'
                    />
                </div>
            </section>
        )
    }

    setStateOption(e) {
        this.setState({
            [e.target.name]: +e.target.value
        })

        if (e.target.name.includes('Exercise')) {
            this.setState({
                [e.target.name.slice(0,-8)]: 0
            })
        }
    }

    getGrade(points) {
        if (points >= 170 && points < 220) {
            return "3";
        } else if (points >= 220 && points < 250) {
            return "4";
        } else if (points >= 250 && points < 270) {
            return "5 (3 уровень)";
        } else if (points >= 270 && points < 280) {
            return "5 (2 уровень)";
        } else if (points >= 280 && points < 290) {
            return "5 (1 уровень)";
        } else if (points >= 290) {
            return "5 (Высший уровень!)";
        } else {
            return "Не сдал :("
        }
    }

    calc(e) {
        e.preventDefault();

        let {speed, power, endurance, fight} = this.state;
        let total = + speed + power + endurance + fight;
        let grade = this.getGrade(total);

        this.setState({
            total,
            grade
        })
    }

}

export default CalcSport