import React from 'react'
import './select.css'

const Select = (props) => {

    const options = props.options.map((option, index) => {
        return (
            <option value={option.value} key={index}>
                {option.title}
            </option>
        )
    })

    return (
        <select className='select' onChange={props.onChangeHandler} name={props.name} defaultValue='0'>
            <option value='0' disabled>...</option>
            {options}
        </select>
    )
}

export default Select