import React, {Component} from 'react'
import './calc.css'
import Header from '../components/header'
import Button from '../components/button'
import InputNumber from '../components/input-number'

class CalcLoan extends Component {

    state = {
        money: 3000000,
        months: 36,
        rate: 5,
        monthlyReturn: 0,
        income: 0,
        incomeWithReinvested: 0,
        averageIncome: 0,
        averageIncomeWithReinvested: 0,
        profitability: 0,
        profitabilityWithReinvested: 0
    }

    render() {
        return (
            <section className='calc'>
                <Header title='Калькулятор доходности беспроцентного займа' />
                <form className='input-data' onSubmit={this.calc.bind(this)}>
                    <InputNumber
                        title='Сумма займа:'
                        value={this.state.money}
                        onChangeHandler={this.setMoney.bind(this)}
                    />
                    <InputNumber
                        title='Время займа в месяцах:'
                        value={this.state.months}
                        onChangeHandler={this.setMonths.bind(this)}
                    />
                    <InputNumber
                        title='Ожидаемая доходность от инвестирования займа в % годовых:'
                        value={this.state.rate}
                        onChangeHandler={this.setRate.bind(this)}
                    />
                    <Button className='submit' title='Рассчитать' />
                </form>
                <div className='output-data'>
                    <p className='output-data__text'>
                        Возврат в месяц: {this.state.monthlyReturn}
                    </p>
                    <ul className='output-data__list output-data__list--row'>
                        <li>
                            Без реинвестирования дохода:
                            <ul className='output-data__list'>
                                <li>Общий доход {this.state.income}</li>
                                <li>Средний доход в месяц {this.state.averageIncome}</li>
                                <li>Доходность % годовых {this.state.profitability}</li>
                            </ul>
                        </li>
                        <li>
                            C реинвестированием дохода:
                            <ul className='output-data__list'>
                                <li>Общий доход {this.state.incomeWithReinvested}</li>
                                <li>Средний доход в месяц {this.state.averageIncomeWithReinvested}</li>
                                <li>Доходность % годовых {this.state.profitabilityWithReinvested}</li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </section>
        )
    }

    calc(e) {
        e.preventDefault();

        let {money, months, rate} = this.state;
        let moneyWithReinvested = money;
        const monthlyReturn = money/months;
        const ratePerMonth = (rate/12).toFixed(2);
        let income = 0;
        let monthlyIncome = 0;
        let incomeWithReinvested = 0;
        let monthlyIncomeWithReinvested = 0;

        for (let month = 0; month < this.state.months; month++) {

            monthlyIncome = money*ratePerMonth/100;
            income += monthlyIncome;
            money -= monthlyReturn;

            monthlyIncomeWithReinvested = moneyWithReinvested*ratePerMonth/100;
            incomeWithReinvested += monthlyIncomeWithReinvested;
            moneyWithReinvested = moneyWithReinvested-monthlyReturn+monthlyIncomeWithReinvested;
        }

        let averageIncome = income/months;
        let averageIncomeWithReinvested = incomeWithReinvested/months;

        let years = months/12;
        let profitability = (income*100/this.state.money)/years;
        let profitabilityWithReinvested = (incomeWithReinvested*100/this.state.money)/years;

        this.setState({
            monthlyReturn: monthlyReturn.toFixed(2),
            income: income.toFixed(2),
            incomeWithReinvested: incomeWithReinvested.toFixed(2),
            averageIncome: averageIncome.toFixed(2),
            averageIncomeWithReinvested: averageIncomeWithReinvested.toFixed(2),
            profitability: profitability.toFixed(2),
            profitabilityWithReinvested: profitabilityWithReinvested.toFixed(2)
        })
    }

    setMoney(e) {
        this.setState({
            money: e.target.value
        })
    }

    setMonths(e) {
        this.setState({
            months: e.target.value
        })
    }

    setRate(e) {
        this.setState({
            rate: e.target.value
        })
    }
}


export default CalcLoan