import React from 'react'
import './controls.css'
import Button from './button'
import Checkbox from './checkbox'

const Controls = (props) => {

    const {calcNames, currentCalc, darkTheme} = props.state
    let buttons = calcNames.map((calc, index) => {
        let current = currentCalc === calc? ' controls__circle--current' : ''
        return (
            <li key={index}>
                <Button
                    className={'controls__circle' + current}
                    value={index}
                    onClickHandler={props.switchCalc}
                />
            </li>
        )
    })

    return (
        <ul className='controls'>
            {buttons}
            <li>
                <Checkbox
                    checked={darkTheme}
                    onChangeHandler={props.switchTheme}
                />
            </li>
        </ul>
    )
}

export default Controls