import React from 'react'
import './radio-area.css'

const RadioArea = (props) => {

    const {name, checkedValue, options} = props

    const buttons = options.map((button, index) => {
        return (
            <label key={index} className='radio-area__row'>
                <input
                    type='radio'
                    className='radio-area__button'
                    name={name}
                    value={button.value}
                    checked={checkedValue === button.value? true : false}
                    onChange={props.onChangeHandler}
                />
                {button.title}
            </label>
        )
    })

    return (
        <div className='radio-area'>
            {buttons}
        </div>
    )
}

export default RadioArea